import {React, useEffect, useState} from 'react'
import {CButton, CCollapse, CFormGroup, CLabel} from '@coreui/react'
import kotakab from '../../../assets/kotakabcombined.json'
import "leaflet/dist/leaflet.css";
import L, { geoJSON } from "leaflet";
import ReactSlider from 'react-slider'
import detailTgl from 'src/assets/detailtgl';
import Select from 'react-select'

const Dashboard = () =>{
    const [collapse, setCollapse] = useState(false)
    const [tanggal, setTanggal] = useState('2020-03-01')
    const [legend, setLegend] = useState(null)
    const [map, setMap] = useState(null)
    const [geoJson, setGeoJson] = useState(null) 
    const [informasi, setInformasi] = useState(null)
    const [pallete, setPallete] = useState(0) 
    // Define Some Contants (Range untuk legend dan pilihan warna tersedia) 
    const arrBatas = [-0.804240, -0.141810, -0.070680, -0.009950, 1.288290]
    const arrColor = [
      ["#b30000","#e34a33","#fc8d59","#fdcc8a","#000000"],
      ['#006d2c','#2ca25f','#66c2a4','#b2e2e2',"#000000"],
      ['#810f7c','#8856a7','#8c96c6','#b3cde3',"#000000"],
      ['#0868ac','#43a2ca','#7bccc4','#bae4bc',"#000000"],
      ['#7a0177','#c51b8a','#f768a1','#fbb4b9',"#000000"]
    ]    
    // Fungsi untuk mendapatkan 
    const getArrRange = (min, max) =>{
      var arr = [min]
      var step = max/5
      for (var i = 0;i<5;i++){
        var val = arr[arr.length-1] + step
        arr.push(val)
      }
      return arr
    }    
    const getColor = (avgchange,color) =>{
      if(arrBatas[1]>avgchange){
        return color[0]
      } else if (arrBatas[2]>avgchange){
        return color[1]
      } else if (arrBatas[3]>avgchange){
        return color[2]
      } else if (avgchange == "na") {
        return color[4]
      } else {
        return color[3]
      }                  
    }      
    const handleRemove = (op, e) =>{
      var tglselected = detailTgl.filter((x)=>x.ds == op)[0]
      var [tempGeo,
        tempinfo,
        templegend,
        minAvgMvmnt, 
        maxAvgMvmnt, 
        rangeAvgMvmnt] = [
          geoJson,
          informasi,
          legend,
          tglselected.min_avg_mvmnt,
          tglselected.max_avg_mvmnt,
          getArrRange(minAvgMvmnt, maxAvgMvmnt)
        ]

      tempinfo.update(false, op)
      setInformasi(tempinfo)
      templegend.update(arrColor[e])
      setLegend(templegend)

      tempGeo.eachLayer((x)=>{
        var mobility = x.feature.properties.mobility.filter((x)=>x.tgl == op)[0]     
        const getStyle = () =>{
          if(mobility){
            x.bindPopup(mobility.avg_change_of_movement + "|" + x.feature.properties.NAME_2)
            var avgchange = mobility.avg_change_of_movement                        
          } else {
            x.bindPopup("N/A" + "|" + x.feature.properties.NAME_2)
            var avgchange = "na"            
          }
          return {
                  fillColor: getColor(avgchange, arrColor[e]),
                  fillOpacity: 1,
                  weight: 0.6,
                  opacity: 1,
                  color: 'black',        
          }                  
        }
        x.setStyle(getStyle())
      })
      setGeoJson(tempGeo)
    }        
    const handleChange = (e) =>{
      setTanggal(detailTgl[e].ds)
      handleRemove(detailTgl[e].ds, pallete)
    }
    const getKuku = (a, tglawal) =>{
      var tglselected = detailTgl.filter((x)=>x.ds == tglawal)[0]
      var minAvgMvmnt = tglselected.min_avg_mvmnt
      var maxAvgMvmnt = tglselected.max_avg_mvmnt
      var rangeAvgMvmnt = getArrRange(minAvgMvmnt, maxAvgMvmnt)
      var mobility = a.properties.mobility.filter((x)=>x.tgl == tglawal)[0]
      if(mobility){
        var avgchange = mobility.avg_change_of_movement        
      } else {
        var avgchange = "na"
      }
      return {
        fillColor:getColor(avgchange,arrColor[pallete]),
        fillOpacity:1,
        color:'black',
        weight: 0.6,
        opacity: 1            
      } 
    }  
    useEffect(()=>{
        alert('Please Wait..')
        var peta = L.map('map',{
          zoomControl: false
        }).setView([-2.548926, 118.0148634], 5);
        var tile = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        })    
        const highlightFeature = (e) =>{
          var layer = e.target;
          layer.setStyle({
              weight: 5,
              color: '#666',
              fillOpacity: 0.7
          });
          info.update(layer.feature.properties, tanggal)
        }    
        function resetHighlight(e) {
          var layer = e.target;
          layer.setStyle({
              weight: 0.6,
              color: 'black',
              fillOpacity: 1
          });
        }    
        function zoomToFeature(e) {        
            peta.fitBounds(e.target.getBounds());
        }    
        const onEachFeature = (feature, layer) =>{
          layer.on({
              mouseover: highlightFeature,
              mouseout: resetHighlight,
              click: zoomToFeature
          });
        }               
        var geo = L.geoJSON(kotakab,{
          onEachFeature:(feature, layer)=>onEachFeature(feature, layer,peta,geo),
          style:(feature)=>getKuku(feature, tanggal)
        })
        var info = L.control();
        info.onAdd = function (peta) {
            this._div = L.DomUtil.create('div', 'info'); // create a div with a class "info"
            this.update();
            return this._div;
        };
        // method that we will use to update the control based on feature properties passed
        info.update = function (props, tgl) {
            this._div.innerHTML = '<h4>'+ (tgl ? tgl : tanggal) + '</h4>' +  (props ?
              '<b>Kota/Kab : </b>'+props.NAME_2+'<br /><b>Change of Movement : </b>' + (
                props.mobility.filter((x)=>x.tgl == tgl)[0] ? 
                props.mobility.filter((x)=>x.tgl == tgl)[0].avg_change_of_movement :
                'N/A'
              ) + '<br/><b>Stay Put: </b>'+ (
                props.mobility.filter((x)=>x.tgl == tgl)[0] ? 
                props.mobility.filter((x)=>x.tgl == tgl)[0].avg_stay_put :
                'N/A'
              )
              : 'Arahkan pointer ke daerah tertentu')
        }; 
         
        var legenda = L.control({position: 'bottomright'});
        legenda.onAdd = function () {
            this._div = L.DomUtil.create('div', 'info legend')
            this.update();
            return this._div;
        };   
        legenda.update = function(props){
            // loop through our density intervals and generate a label with a colored square for each interval
            this._div.innerHTML = ""
            for (var i = 0; i < arrBatas.length; i++) {
              this._div.innerHTML +=
                  '<i style="background-color:'
                  + 
                  (props ? props[i] : arrColor[pallete][i]) 
                  +
                  '"></i>' 
                  +
                  (
                    arrBatas[i + 1] ? 
                    arrBatas[i] + ')&ndash;(' + arrBatas[i + 1] + ')<br>' 
                    : 
                    'N/A'
                  );
          }          
        }

        legenda.addTo(peta);  
        info.addTo(peta);
        geo.addTo(peta);
        tile.addTo(peta); 

        setLegend(legenda) 
        setInformasi(info)  
        setGeoJson(geo)   
        setMap(peta)           
    },[])
    return(
        <>
        <div className="card d-flex flex-column justify-content-center" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
          <div className="card-header d-flex justify-content-between" style={{width:'100%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div><a style={{fontWeight:'500', fontSize:'15px'}}>Dashboard Control Panel</a></div>
            <div><div onClick={()=>setCollapse(!collapse)} className="btn btn-secondary btn-sm" style={{borderRadius:'12px'}}><i class={collapse ? 'fas fa-chevron-down' : 'fas fa-chevron-up'}></i></div></div>
          </div>
          <CCollapse
          show={collapse}
          >
          <div className="d-flex flex-row mx-3 mb-3 mt-3 justify-content-center">
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0', width:'50%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <div className="d-flex flex-column justify-content-center">
                <div style={{width:'30%'}}><a style={{fontWeight:'300', fontSize:'23px'}}>Linimasa:</a></div>
                <div className="d-flex flex-row justify-content-center">
                  <div className="mr-2 d-flex align-items-center"><a style={{fontWeight:'300',fontSize:'15px'}}>01/03/2020</a></div>
                  <div className="mr-2 d-flex align-items-center" style={{width:'50%'}}>
                    <ReactSlider
                      min={0}
                      max={detailTgl.length-1}
                      className="horizontal-slider"
                      thumbClassName="example-thumb"
                      trackClassName="example-track"
                      onChange={(e)=>handleChange(e)}
                    />                
                  </div>
                  <div className="d-flex align-items-center"><a style={{fontWeight:'300',fontSize:'15px'}}>12/08/2021</a></div>
                </div>                   
              </div>            
            </div>
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0',width:'30%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <CFormGroup>
                <CLabel><a style={{fontWeight:'300', fontSize:'23px'}}>Daerah :</a></CLabel>
                <Select
                />
              </CFormGroup>
            </div> 
            <div className="card px-3 py-3 mr-2" style={{marginBottom:'0',width:'30%', borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
              <CFormGroup>
                <CLabel><a style={{fontWeight:'300', fontSize:'23px'}}>Palet Warna</a></CLabel>
                <Select
                defaultValue={{label:'Pilihan 1', value:'0'}}
                styles={{
                  // Fixes the overlapping problem of the component
                  menu: provided => ({ ...provided, zIndex: 9999 })
                }}               
                onChange={(e)=>{
                  handleRemove(tanggal,parseInt(e.value))
                  setPallete(parseInt(e.value))
                }}
                options={[
                  {label:'Pilihan 1', value:'0'},
                  {label:'Pilihan 2', value:'1'},
                  {label:'Pilihan 3', value:'2'},
                  {label:'Pilihan 4', value:'3'},
                  {label:'Pilihan 5', value:'4'},                  
                ]}
                />
              </CFormGroup>
            </div>            
          </div>            
          </CCollapse>
        </div>
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
          <div id="map" style={{ height: "60vh" }}></div>           
        </div>
        </>
    )
}

export default Dashboard